<template>
  <div class="tmpl">
    <div class="main-body" ref="wrapper" :style="{ height: (wrapperHeight-50) + 'px' }">
      <mt-loadmore  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :autoFill="isAutoFill">
        <ul class="mui-table-view mui-grid-view">
          <li v-for="(item,index) in datas" :key="index" class="mui-table-view-cell mui-media mui-col-xs-6">
            <img :src="item.url" alt="">
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
export default {
  name: "goodslist",
  data() {
    return {
      datas: [],
      //可以进行上拉
      allLoaded: false,
      //是否自动触发上拉函数
      isAutoFill: false,
      wrapperHeight: 0,
      page:2
    };
  },
  created() {
    this.loadFrist();
  },
  mounted() {
    // 父控件要加上高度，否则会出现上拉不动的情况
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top;
   
  },
  methods: {
    // 上拉加载
    loadBottom() {
      this.loadMore();
    },
    // 下来刷新加载
    loadFrist() {
      this.$axios
        .get(`https://www.apiopen.top/meituApi?page=${this.page}`)
        .then(response => {
            console.log(response);
          this.allLoaded = false; // 可以进行上拉
          this.datas = response.data.data;
          // this.$refs.loadmore.onTopLoaded();
          this.page ++;
        })
        .catch(error => {
          console.log(error);
          alert("网络错误，不能访问");
        });
    },
    // 加载更多
    loadMore() {
      this.$axios
        .get(`https://www.apiopen.top/meituApi?page=${this.page}`)
        .then(response => {
          // concat数组的追加
          this.datas = this.datas.concat(response.data.data);
          if (response.data.data.length <= 0) {
            this.allLoaded = true; // 若数据已全部获取完毕
          }
          this.page++;
          this.$refs.loadmore.onBottomLoaded();
        })
        .catch(error => {
          console.log(error);
          alert("网络错误，不能访问");
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main-body {
  /* 加上这个才会有当数据充满整个屏幕，可以进行上拉加载更多的操作 */
  overflow: scroll;
}
li{
    height: 40px;
    background: #000;
    margin-bottom: 40px;
}
</style>


